import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

import title from '../../images/logo-shadow.webp'
import mainBg from '../../images/Header.webp'
import mainBgMedium from '../../images/mainBg-medium.webp'
import mainBgSmall from '../../images/Header-small.webp'


const Hero = () =>{

    const [visible, setVisible] = useState(true)

    const oscurecerBg = () =>{
        setTimeout(() => {
            setVisible(true)
        }, 1000);
    }

    useEffect(()=>{
        window.addEventListener('load', oscurecerBg())
        return(
            window.removeEventListener('load', oscurecerBg())
        )
    },[])


    return(
        <Container>
            <Filter visible={visible}>
               <TextGroup>
                    <Title 
                        src={title} 
                        visible={visible}
                        alt="Cuentos de Camino" 
                    />
                    <P visible={visible}>
                        
                        Revive los Momentos que Marcaron a Toda una Generación
                    </P>
               </TextGroup>
               <Div>

               <VideoC>
                    <VideoR>
                        <Video src="https://www.youtube.com/embed/qostNvjf8YU?controls=0" title="YouTube video player"  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></Video>
                    </VideoR>
                </VideoC>
               </Div>
            </Filter>
        </Container>
    );
}
const Container = styled.div`
    min-height: 100vh;
    
    
    overflow: hidden;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: top center;
    background-color: black;
    background-image: url(${mainBgSmall});

    @media(min-width: 600px){
        background-image: url(${mainBgMedium});
    }
    @media(min-width: 900px){
        background-image: url(${mainBg});
    }
`;
const Filter = styled.div`
    min-height: 100vh;
    transition: background-color 1s;
    background-color: ${props=>props.visible ? 'rgba(27, 27, 27, .3)' : ''};
    display: flex;
    flex-direction: column;
    justify-content: center;
    

    @media(min-width: 900px){
        background-color: transparent;

        padding: 50px 10% 0px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: center;
    }
`;
const TextGroup = styled.div`
    margin-top: 230px;

    @media(min-width: 900px){
        margin-top: 0px;
        grid-column: -2 / -1;
        grid-row: 1 / -1;
    }
`;
const Title = styled.img`
    display: block;
    width: 350px;
    margin: 0 auto 10px;
    transition: opacity 1s .5s, transform 1s .5s;
    opacity: ${props=>props.visible ? '1' : '0'};
    transform: translateY(${props=>props.visible ? '0px' : '30px'});

    @media(min-width: 900px){
        width: 450px;
    }    
`;
const P = styled.p`
    color: #fff;
    max-width: 400px;
    margin: 0px auto;
    text-align: center;
    font-size: 1.4rem;
    line-height: 30px;
    transition: opacity 1s 1s, transform 1s 1s, background-color 1s 1s;
    opacity: ${props=>props.visible ? '1' : '0'};
    transform: translateY(${props=>props.visible ? '0px' : '30px'});
    
    margin: 10px auto 0px;
    font-weight: 700;

    @media(min-width: 900px){
        text-align: right;
        font-size: 2rem;
        line-height: 40px;
        background-color: ;
        text-shadow: 0 0 3px black,
        0 0 5px black,
        0 0 7px black,
        0 0 10px black
        ;
        transform: translate(0px, ${props=>props.visible ? '0px' : '30px'});
    }
`;
const Div = styled.div`
background-image: linear-gradient(rgba(0,0,0,.1) , black 50%);
box-shadow: 0 0px 1px white, 
0 0px 1px black,
0 0px 10px black,
0 0px 15px black;
grid-column: 1 / -1;

@media(min-width: 900px){
    margin-top: 220px;
    margin-bottom: 10px;

}
`;
const VideoC = styled.div`
    width: 80%;

    margin: 50px auto 50px;
    min-width: 340px;
    max-width: 700px;
    
`;
const VideoR = styled.div`
    height: 0;
    padding-bottom: 56.25%;
    position: relative;
`;
const Video = styled.iframe`
    position: absolute; 
    width: 100%;
    height: 100%;
    border: none;
`;
export default Hero;